import React,{useReducer} from 'react';
import ReactDOM from 'react-dom';
import Nav from './components/Nav';
import {Route,BrowserRouter as Router,Switch,Redirect} from 'react-router-dom'
import { UserContext } from './context/UserContext';
/* Importing Css Files */
import './index.css'
import 'bootstrap/dist/css/bootstrap.min.css'
/* Importing Display Components */
import Login from './components/Login' 
import Signup from './components/Signup' 
import Dashboard from './components/Dashboard' 
import GuestDashboard from './components/GuestDashboard' 
import NotFound from './components/NotFound' 

const initialState = {
	'Login':false,
	'userId':1,
	'userName':''
}



const reducer = (state,action) => {
	if(action.type === 'LOGIN'){
		let newState = {'Login':true}
		if(action.payload && action.payload.username) newState['userName'] = action.payload.username
		return {...state,...newState}
	}
	if(action.type === 'LOGOUT'){
		return {...state,'Login':false}
	}
	return state;
}

const App = () => {	

  const [state,dispatch] = useReducer(reducer,initialState)
    

  return(
     <UserContext.Provider  value={{state,dispatch}}>
		<Router>
		   {state.Login?<Redirect from='/' to='/dashboard' />:<Redirect from='/' to='/login' />}
				<Nav state={state} dispatch={dispatch} />
				<Switch>
					<Route exact path='/login' component={Login} />
					<Route exact path='/dashboard' component={(state.userName !== 'Guest')?Dashboard:GuestDashboard} />
					<Route exact path='/signup' component={Signup} />
					<Route  path='*' component={NotFound} />
				</Switch>
		</Router>
	 </UserContext.Provider >
	)
}


ReactDOM.render(<App />,document.getElementById('root')) 


