import React from 'react';
import {Formik,Form,Field,ErrorMessage} from 'formik';
import {useHistory} from 'react-router-dom';


export default function SignupForm(){
	let history = useHistory();
	return(
		<Formik 
				initialValues={{
					username:'',
					email:'',
					password:''
				}}
				
				onSubmit={(values,actions) => {
					fetch("/signup",{
						method:"POST",
						headers:{"Content-Type":"application/json"},
						body:JSON.stringify({...values})
					})
					.then((res) => {
						if(res.ok){
							history.replace('login')
						}else{
							alert('Failed')
						}
						
					})
					.catch(() => {
						alert('Error');
					})
					.finally(() => {
						actions.setSubmitting(false)
					})
				}}
				
				validate={values => {
					const emailRegex = /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i;
					const errors = {};
					if(!values.username){
						errors.username = 'Please Enter a Username'
					}
					if(!values.email || !emailRegex.test(values.email)){
						errors.email = 'Please Enter a Valid Email'
					}
					if(!values.password){
						errors.password = 'Please Enter a Password'
					}
					return errors;
				}}
			>
		{() => (
			<Form name="signupForm mt3" >
				<div className='row'>
					<div className='form-group col-sm '>
						<label htmlFor="username"  >Username : </label>
						<Field name="username" className='form-control' />
						<p className='text-danger'><ErrorMessage  name="username" /></p>
					</div>
					<div className='form-group col-sm '>
						<label htmlFor="email"  >Email : </label>
						<Field name="email" className='form-control' />
						<p className='text-danger'><ErrorMessage   name="email" /></p>
					</div>
					<div className='form-group col-sm '>
						<label htmlFor="password"  >Password: </label>
						<Field name="password" className='form-control' />
						<p className='text-danger'><ErrorMessage   name="password" /></p>
					</div>	
			 </div>
					<div className='Btn' style={{textAlign:'center'}}>
						<button type='submit' className='btn btn-primary'>Submit</button>
					</div>
			 
			</Form>
		)}
		</Formik>
	)
}
