import React from 'react';
import LoginForm from './LoginForm'
import { UserContext } from '../context/UserContext';


export default function Login(){
	const LoginContext = React.useContext(UserContext);
	
	return(
		<section className='form mt-3'>
			<LoginForm LoginContext={LoginContext} />
			<p style={{textAlign:'center',fontWeight:'800',marginTop:'1rem'}}>OR</p>
			<div className='Btn' style={{textAlign:'center'}}>
				<button  className='btn btn-primary' onClick={() => LoginContext.dispatch({type:'LOGIN',payload:{username:'Guest'}}) } >Try as Guest</button>
			</div>
		</section>
	)
}