import React from 'react';
import useIDBDisplayData from './useIDBDisplayData';

export default function useIDBUpdateData({db,id,task,section,setResult}){
	   if(task.length < 1 || section.length < 1 || !db || !id){
	           console.log('Recieved Empty VALUES')
	   }
	
	   // grab the new values entered into the form fields and store them in an object ready for being inserted into the DB
        let newItem = {
            id:parseInt(id),task,section
        };

        // open a read/write db transaction, ready for adding the data
        let transaction = db.transaction(['todo_app'], 'readwrite');

        // call an object store that's already been added to the database
        let objectStore = transaction.objectStore('todo_app');

        // Make a request to add our newItem object to the object store
        let request = objectStore.put(newItem);
		
        request.onsuccess = function () {
            console.log('Success editing')
        };

        // Report on the success of the transaction completing, when everything is done
        transaction.oncomplete = function () {
            console.log('Transaction completed: databaseis updated finished.');
            // update the display of data to show the newly added item, by running displayData() again.
            useIDBDisplayData({db,setResult})
        };

        transaction.onerror = function () {
            console.log('Transaction not opened due to error');
        };
		return 1;
}
