import React,{useState,useEffect} from 'react';
import Navbar from 'react-bootstrap/Navbar';
import Nav from 'react-bootstrap/Nav';
import {Link} from 'react-router-dom';


function NavigationBar({state:{Login,userName},dispatch}){
	return(
		<>
			<Navbar expand="lg" bg="primary" variant="dark" >
				<Navbar.Brand href="#">Hi {userName ||'User'}!</Navbar.Brand>
				<Nav className="navlink" >
				 {Login?<Link to="/dashboard">Dashboard</Link>:null}
				 {!Login?<Link to="/signup" >Signup</Link>:null}
				 {!Login?<Link to="/login" >Login</Link>:null}
				 {Login?<Link to="/logout" onClick={() => dispatch({'type':'LOGOUT',userName:''})}>Logout</Link>:null}
				</Nav>
			</Navbar>
		</>
	)
}

export default React.memo(NavigationBar)