import React,{useState,useEffect} from 'react';
import Button from './Button'
import Test from './Test'
import useIDBDisplayData from './useIDBDisplayData';

export default function GuestMainScreen({db}){
	
	const [result,setResult] = useState([])
	const [Form,setForm] = useState({'ADD':false,'EDIT':false})
	
	function handleDate(){
		let Today = new Date()
		return  Today.toLocaleDateString()
	}
	
	useEffect(() => {
		if(typeof db === 'object'){
			console.log('Main screen')
			console.log(typeof db)
			useIDBDisplayData({db,setResult})
		}
	},[db])
	
	return(
		<article style={{width:'90vw',margin:'auto'}}>
			<div style={{display:'flex',justifyContent:'space-between',width:'90%',margin:'auto'}}>
				<p >Today : {handleDate()}</p>
				<Button name={'+'} handleClick={() => setForm({'ADD':true,'EDIT':false})} styles={{borderRadius:'50%',fontSize:'1rem'}} />
			</div>
			<Test db={db} result={result} Form={Form} setForm={setForm} setResult={setResult} />
		</article>
	)
}