import React from 'react';

export default function useIndexDB({name,setDB}){
	let db;
	console.log('I am from useIndexDB ')
    let request = window.indexedDB.open(name, 1);

    // onerror
    request.onerror = function () {
        console.log(`Database ${name} failed to open`);
    };

    // onsuccess 
    request.onsuccess = function () {
        console.log(`Database ${name} opened successfully`);
        db = request.result;
		console.log(typeof db)
		console.log(db)
		console.log(typeof setDB)
		setDB(db)
        // Run the displayData() function to display the notes already in the IDB
        //displayData();
    };
	
	// Setup the database tables if this has not already been done
    request.onupgradeneeded = function (e) {
        // Grab a reference to the opened database
        let db = e.target.result;

        // Create an objectStore to store our notes in (basically like a single table)
        // including a auto-incrementing key
        let objectStore = db.createObjectStore(name, {
            keyPath: 'id',
            autoIncrement: true
        });

        // Define what data items the objectStore will contain
        objectStore.createIndex('task', 'task', {
            unique: false
        });
        objectStore.createIndex('section', 'section', {
            unique: false
        });

        console.log('Database setup complete');
    };
}