import React from 'react';
import {Formik,Form,Field,ErrorMessage} from 'formik';

export default function LoginForm({LoginContext}){
	
	return(
			<Formik 
				initialValues={{
					password:'',
					email:'',
				}}
				
				onSubmit={(values,actions) => {
					fetch('/login',{
						method:"POST",
						headers:{"Content-Type":"application/json"},
						body:JSON.stringify({...values})
					})
					.then((res) => {
						if(res.ok){
							res.json().then(data => {
									console.log('Login Form Succesfully Recieved Data')
									console.log(data)
									LoginContext.dispatch({type:'LOGIN',payload:{'username':data.username}})
							})	
						}
						else{
							console.log('Login Form Recieved Error')
							res.json().then(err => alert(err.msg))
						}
					})
					.catch(() => {
						alert('Error');
					})
					.finally(() => {
						actions.setSubmitting(false)
					})
				}}
				
				validate={values => {
					const emailRegex = /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i;
					const errors = {};
					if(!values.password){
						errors.password = 'Please Enter Password'
					}
					if(!values.email || !emailRegex.test(values.email)){
						errors.email = 'Please Enter a Valid Email'
					}
					return errors;
				}}
			>
		{() => (
			<Form name='loginForm'>
			  <div className='row'>
				<div className='form-group col-sm'>
					<label htmlFor="email" >Email : </label>
					<Field name="email"  className='form-control' />
					<p className='text-danger'><ErrorMessage   name="email" /></p>
				</div>
				<div className='form-group col-sm'>
					<label htmlFor="password" >Password : </label>
					<Field name="password"  type="password"  className='form-control'/>
					<p className='text-danger'><ErrorMessage  name="password" /></p>
				</div>
			 </div>
			 <div className='Btn' style={{textAlign:'center'}}>
				<button type='submit' className='btn btn-primary' >Submit</button>
			</div>
			</Form>
		)}
		</Formik>
	)
}
