import React from 'react';
import useIDBDisplayData from './useIDBDisplayData';


export default function useIDBDelteData({db,e,setResult}){
		console.log('Inside useIDBDelteData')
        let noteId = Number(e.target.id);

        // open a database transaction and delete the task, finding it using the id we retrieved above
        let transaction = db.transaction(['todo_app'], 'readwrite');
        let objectStore = transaction.objectStore('todo_app');
        let request = objectStore.delete(noteId);

        // report that the data item has been deleted
        transaction.oncomplete = function () {
			useIDBDisplayData({db,setResult})
        };


  }
