import React from 'react';

export default function EDITFORM({Form,setForm,setSection,setTask,handleEdit}){
	return(
		<section style={{position:'absolute',backgroundColor:'orange',padding:'1rem',bottom:'0',right:'0'}}>
					<button type='button' style={{position:'absolute',left:'0',top:'0',backgroundColor:'blue',color:'white'}} onClick={() => setForm({...Form,'EDIT':false})}>Close</button>
					<h2 style={{marginTop:'1rem'}}>Edit Todo</h2>
				    <form>
					  <div>
						<label htmlFor="task">Task Name</label>
						<input id="task" type="text" onChange={(e) => setTask(e.target.value)}  required />
					  </div>
					  <div>
						<label htmlFor="section">Section Name</label>
						<input id="section" type="text" onChange={(e) => setSection(e.target.value)} required />
					  </div>
					  <div>
						<button type='submit' onClick={handleEdit}>Create new Task</button>
					  </div>
				</form>
		</section>
	)
}

				