import React,{useState} from 'react';
import useIDBTransaction from './useIDBTransaction';
import useIDBDelteData from './useIDBDelteData';
import useIDBUpdateData from './useIDBUpdateData';
import ADDFORM from './ADDFORM';
import EDITFORM from './EDITFORM';


export default function Test({db,result,Form,setResult,setForm}){
	
	const [task,setTask] = useState('');
	const [section,setSection] = useState('');
	const [id,setId] = useState(undefined);
	
	function handleClick(e){
		e.preventDefault();
		console.log('Going to run DB transcation')
		useIDBTransaction({db,task,section,setResult})
	}
	
	function handleEdit(e){
		e.preventDefault();
		console.log('Going to run edit transcation')
		console.log(id)
		useIDBUpdateData({db,id,task,section,setResult})
	}
	
	function showEditForm(e){
		setId(e.target.id)
		setForm({'ADD':false,'EDIT':true})
	}
	
	function handleDelete(e){
		e.preventDefault();
		console.log('Going to run delete transactions')
		useIDBDelteData({db,e,setResult})
	}
	
	const allResults = result && result.map((item) => {
		return <li key={item.id}>
			<p style={{textTransform:'Capitalize',color:'Black',fontWeight:'900'}}>{item.section}</p>
			<p style={{textTransform:'Capitalize'}}>{item.task}</p>
			<button id={item.id} onClick={(e) => showEditForm(e)}>Edit</button>
			<button id={item.id} onClick={(e) => handleDelete(e)}>Delete</button>
		</li>
	})
	
	return(
		<>
			<section style={{textAlign:'center'}}>
				<h2 style={{marginBottom:'2rem'}}>Added Task</h2>
				<ul className='taskList'>
					{allResults?allResults:'No Task Created'}
				</ul>
			</section>
			{Form.ADD?<ADDFORM Form={Form} setForm={setForm}  setTask={setTask} setSection={setSection} handleClick={handleClick} />:null}
			{Form.EDIT?<EDITFORM Form={Form} setForm={setForm}  setTask={setTask} setSection={setSection} handleEdit={handleEdit} />:null}
		</>
	)
	
}
