import React,{useState,useEffect} from 'react';
import SideBar from './SideBar';
import MainScreen from './MainScreen';

export default function Dashboard(){
	
	const [data,setData] = useState([]);
	const [filter,setFilter] = useState('All');
	
	useEffect(() => {
		fetch('/user/dashboard').then((res) => {
			if(res.ok){
				res.json().then(Rdata => {
						console.log('Dashboard Get  Recieved Data')
						console.log(Rdata)
						if(Rdata && Rdata.data.length > 0) setData(Rdata.data)  
					})
			}
			else{
				console.log('Dashboard Get  Recieved Error')
			}
		})
		.catch(() => {
			alert('Error');
		})

	},[])
	
	
	
	return(
		<section className='dashboard'>
				<SideBar  data={data} setFilter={setFilter} />
				<MainScreen data={data} setData={setData} filter={filter}  />
		</section>
	)
}