import React from 'react';

export default function SideBar({data,setFilter}){
	
	let sectionList = [];
	
	data.forEach(taskObj => {
		console.log(taskObj)
		if(taskObj.section){
			if(!sectionList.includes(taskObj.section)){
				sectionList.push(taskObj.section)
			}
		}
	})
	
	
	return(
		<article className='sidebar'>
			<div onClick={(e) => setFilter(e.target.id)} style={{display:'flex',flexDirection:'column'}}>
				<button className='btn btn-primary' id={'All'} style={{marginBottom:'0.5rem'}} >All</button>
				{sectionList.map(sec => {
					console.log(sec)
					return <button key={sec} className='btn btn-primary'  id={sec} style={{marginBottom:'0.5rem'}} >{sec}</button>
					})
				}
			</div>
		</article>
	)
}