import React,{useState,useEffect} from 'react';
import Button from './Button'
import CustomForm from './CustomForm'
import {useHistory} from 'react-router-dom';


export default function MainScreen({db,data,filter,setData}){
	let history = useHistory();
	let FilterResult;
	const [Form,setForm] = useState({'ADD':false})
	const [task,setTask] = useState('');
	const [section,setSection] = useState('');
	const [id,setId] = useState(undefined);

	function handleDate(){
		let Today = new Date()
		return  Today.toLocaleDateString()
	}
	
	function handleAdd(e){
		e.preventDefault()
		alert('Request for adding')
		fetch('/user/dashboard/create',{
				method:"POST",
				headers:{"Content-Type":"application/json"},
				body:JSON.stringify({'task':task,'section':section})
		}).then((res) => {
			if(res.ok){
				res.json().then(newdata => {
						console.log('On creating  Recieved Data')
						console.log(newdata.task)
						setData([...data,newdata.task])
					})
			}
			else{
				console.log('Dashboard Get  Recieved Error')
				alert('Error in data')
			}
		})
		.catch(() => {
			alert('Error');
		})
	}
	
	function handleUpdate(e){
		e.preventDefault()
		alert('Request for updating')
		fetch('/user/dashboard/update',{
				method:"POST",
				headers:{"Content-Type":"application/json"},
				body:JSON.stringify({'id':e.target.id})
		}).then((res) => {
			if(res.ok){
				res.json().then(recievedata => {
						console.log('Update Get  Recieved Data')
						console.log(recievedata)
						console.log(recievedata.id)
						let newData;
					    newData = data.map(t => {
							if(t._id === recievedata.id){
								console.log('found')
								t['completed'] = true
							}
							return t
						 })
					    console.log(newData)
						setData(newData)
					})
			}
			else{
				console.log('Dashboard Get  Recieved Error')
				alert('Error in data')
			}
		})
		.catch(() => {
			alert('Error');
		})
	}
	
	function handleDelete(e){
		let requiredId = e.target.id
		e.preventDefault()
		alert('Request for deleting')
		fetch('/user/dashboard/delete',{
				method:"POST",
				headers:{"Content-Type":"application/json"},
				body:JSON.stringify({'id':e.target.id})
		}).then((res) => {
			if(res.ok){
				console.log('Delete Get  Recieved Data')
				console.log(requiredId)
			    let newData = data.filter(task => task._id != requiredId)
				console.log(newData)
				setData(newData)
			}
			else{
				console.log('Dashboard Get  Recieved Error')
			}
		})
		.catch(() => {
			alert('Error');
		})
	}
	
	const DataResult = data.map(taskObj => {
		return <div key={taskObj._id} className='task-items' >
		<h4>{taskObj.task}</h4>
		{!taskObj.completed?<button className='btn btn-primary'  id={taskObj._id} onClick={(e) => handleUpdate(e)}>Complete</button>:<p className='btn btn-success'>Done!</p>}
		<button className='btn btn-danger' id={taskObj._id} onClick={(e) => handleDelete(e)} >Delete</button>
		</div>
	})
	
	if(filter !== 'All'){
		console.log('Main Screen')
		let myFilter = data.filter(taskObj => taskObj.section === filter )
		console.log(myFilter)
		FilterResult = myFilter.map(taskObj => {
		return (
			<div key={taskObj._id} className='task-items' >
				<h4>{taskObj.task}</h4>
				{!taskObj.completed?<button className='btn btn-primary'  id={taskObj._id} onClick={(e) => handleUpdate(e)}>Complete</button>:<p className='btn btn-success my-auto'>Done!</p>}
				<button className='btn btn-danger' id={taskObj._id} onClick={(e) => handleDelete(e)} >Delete</button>
			</div>
			)
		})
	}
	
	return(
	<section>
		<article className='main'>
			<div style={{display:'flex',justifyContent:'space-between',width:'90%',margin:'auto'}}>
				<p >Today : {handleDate()}</p>
				<Button name={'+'} handleClick={() => setForm({'ADD':true})} styles={{borderRadius:'50%',fontSize:'1rem'}} />
			</div>
			  {(Form.ADD)?<CustomForm type={'Create'} Form={Form} setForm={setForm}  setTask={setTask} setSection={setSection} handleMe={handleAdd} />:null}
		</article>
		<article style={{display:'flex',justifyContent:'center',flexDirection:'column',width:'90%',marginTop:'2rem'}} >
			{(filter === 'All')?DataResult:FilterResult || null}
		</article>
	</section>
	)
}